




<!-- 
    1:
	2: '' id,
	3 '' id,
	4 '' id,
-->
<template>
<div>
					<!--  -->
	<el-table :data="article.slice( (currentPage-1)*pageSize,pageSize*currentPage)" border height="600">   
		<el-table-column
			prop="date"
			label=""
			sortable
			>
		</el-table-column>

		<el-table-column
			prop="title"
			label=""
			
		>
		</el-table-column>
		<el-table-column
			prop="writer"
			label="">
		</el-table-column>
		<!-- 
			filters ,    [{text:,value:}]
			filter-method(val,row)  valfiltersvalue,row ;,boolean
		 -->
		<el-table-column
			prop="kind"
			:filters="filt"
			:filter-method="filterTag"
			:filter-multiple="filt_mult"
			label=""
		>
		</el-table-column>

		<el-table-column
			prop="reading"
			label=""
			sortable>
		</el-table-column>

		<el-table-column label="">
			<template slot-scope="scope">
				<el-button type="text" size="small" @click="check(scope.row._id)"></el-button><!-- scope.rowid,slot-scope="scope" -->
				<el-button type="text" size="small" @click="edit(scope.row._id)"></el-button>
				<el-button type="text" size="small" @click="del(scope.row._id)" v-loading.fullscreen="loading"></el-button>
			</template>
		</el-table-column>
	</el-table>


	<fenye @fenyeData="pageData(arguments)" :totle="article.length" :defaultPageSize="pageSize"></fenye>
	
</div>
</template>

<script>
import fenye from '../../../client/modules/fenye.vue'
export default{
	name:"all",
	data(){
		return{
			currentPage:1,
			pageSize:10,
			url:"http://127.0.0.1:3000",
			article:[],
			// 
			filt:[{
				text:"Node.js",
				value:"Node.js"
			},{
				text:"h5+css3",
				value:"h5+css3"
			},{
				text:"PHP",
				value:"PHP"
			},{
				text:"Vue",
				value:"Vue"
			},{
				text:"Mongodb",
				value:"Mongodb"
			},],
			filt_mult:false,    //
			loading:false,
		}
	},
	components: {
		fenye
	},
	created() {
		this.all()
	},
	methods:{
		pageData(pages){
			this.pageSize=pages[0];
			this.currentPage=pages[1];
		},

		// 
		filterTag(val,row){
			//   valfiltersvalue,
			//   rowvalue;
			return row.kind==val;
		},

		// 
		all(){
			this.$axios.get(this.url+"/look")   //
			.then(res=>{
				this.article=res.data;
				
			})
			.catch(err=>{
				console.log(err)
			})
		},

		// ,id
		check(id){
			this.$router.push({
				 path: '/check',
				 query:{
					 id:id
				 }
			})
		},

		// ,id
		edit(id){
			this.$router.push({
				 path: '/edit',
				 query:{
					 id:id
				 }
			})
		},

		// id,1

		del(id){
			this.$confirm("","",{
				confirmButtonText: '',
				cancelButtonText: '',
				type: 'warning',
			})
			.then((r)=>{
				if(r=="confirm"){
					this.loading=true;    //
					
					this.$axios.get(this.url+"/del",{params:{
						id:id
					}})
					.then(res=>{
						console.log(res.data)
						if(res.data=='1'){   //  1
							let that=this;
							setTimeout(function(){
								that.loading=false;
								that.all()    //
								that.$message({
									message:"",
									type:"success",
									durations:1500,
								})
							},1000)
						}else{
							let that=this;
							setTimeout(function(){
								that.loading=false;
								that.$message({
									message:",",
									type:"error",
									durations:1500,
								})
							},1000)
							
						}
					})
					.catch(err=>{
						this.loading=false;
						this.$message({
							message:"",
							type:"error",
							durations:1500,
						})
					})
				} 
			})
			.catch((e)=>{

			})
		},

		// 
		sizeChange(size){
			this.pageSize=size;
			// console.log(size);
		},

		// 
		crtChange(page){
			this.currentPage=page
			// console.log(page);
		}
	}
}
</script>

<style scoped="scoped">
	/deep/ .el-table .cell{
		white-space: nowrap;
		text-overflow: ellipsis;
		overflow: hidden;
	}
	/deep/.el-table td{
		text-align: center;
	}
	/deep/.el-table th{
		text-align: center;
	}
	
</style>



