




<!-- 
    1:
	2: '' id,
	3 '' id,
	4 '' id,
-->
<template>
<div>
					<!--  -->
	<el-table :data="article.slice( (currentPage-1)*pageSize,pageSize*currentPage)" border height="600" :default-sort ="{prop:'reading',order:'descending'}">   
		<el-table-column
			prop="date"
			label=""
			sortable
			>
		</el-table-column>
        
		<el-table-column
			prop="title"
			label=""
			>
		</el-table-column>

		<el-table-column
			prop="reading"
			label=""
			sortable
			
			>
		</el-table-column>

		<el-table-column
			prop="writer"
			label="">
		</el-table-column>
        
		<!-- 
			filters ,    [{text:,value:}]
			filter-method(val,row)  valfiltersvalue,row ;,boolean
		 -->
		<el-table-column
			prop="kind"
			:filters="filt"
			:filter-method="filterTag"
			:filter-multiple="filt_mult"
			label=""
		>
		</el-table-column>
	</el-table>


	<fenye @fenyeData="pageData(arguments)" :totle="article.length" :defaultPageSize="pageSize"></fenye>
</div>
</template>

<script>
import fenye from '../../client/modules/fenye.vue'
export default{
	name:"all",
	data(){
		return{
			currentPage:1,
			pageSize:10,
			url:"http://127.0.0.1:3000",
			article:[],
			// 
			filt:[{
				text:"Node.js",
				value:"Node.js"
			},{
				text:"h5+css3",
				value:"h5+css3"
			},{
				text:"PHP",
				value:"PHP"
			},{
				text:"Vue",
				value:"Vue"
			},{
				text:"Mongodb",
				value:"Mongodb"
			},],
			filt_mult:false    //
		}
	},
	components: {
		fenye
	},
	created() {
		this.all()
	},
	methods:{
		// 
		pageData(pages){
			this.pageSize=pages[0];
			this.currentPage=pages[1];
		},

		// 
		filterTag(val,row){
			//   valfiltersvalue,
			//   rowvalue;
			return row.kind==val;
		},

		// 
		all(){
			this.$axios.get(this.url+"/look")   //
			.then(res=>{
				this.article=res.data;
				
			})
			.catch(err=>{
				this.$message({
					message:",",
					type:"error",
					durations:1000,
				})
			})
		},

		// 
		sizeChange(size){
			this.pageSize=size;
			// console.log(size);
		},

		// 
		crtChange(page){
			this.currentPage=page
			// console.log(page);
		}
	}
}
</script>

<style scoped="scoped">
	/deep/ .el-table .cell{
		white-space: nowrap;
		text-overflow: ellipsis;
		overflow: hidden;
	}
	/deep/.el-table td{
		text-align: center;
	}
	/deep/.el-table th{
		text-align: center;
	}
</style>




