import Vue from 'vue'
import Router from 'vue-router'

// 
import index from '@/components/client/index'  //
import articles from '@/components/client/article'  //
import detail from '@/components/client/detail'  //
import search from '@/components/client/search'  //
import login from '@/components/client/login'  //

import h from '@/components/client/nav/h5'  //
import node from '@/components/client/nav/Node'  //
import mongodb from '@/components/client/nav/MongoDB'  //
import php from '@/components/client/nav/php'  //
import vue from '@/components/client/nav/Vue'  //

// 
import manage from '@/components/manage/manage'  //
import test from '@/components/manage/test'  //

import pandect from '@/components/manage/pandect/pandect'  //
import add from '@/components/manage/about/add'  //
import all from '@/components/manage/about/about_look/all'   //
import check from '@/components/manage/about/about_look/check'  //
import edit from '@/components/manage/about/about_look/edit'  //
import hot from '@/components/manage/about/hot'  //
import allDiscuss from '@/components/manage/comment/sypl'  //
import replyCmt from '@/components/manage/comment/replyCmt'  //
import noReply from '@/components/manage/comment/noReply'  //


Vue.use(Router);

export default new Router({
	mode:"history",
	routes: [
    {
      path: '/',   //
      component: index, 
		children:[
			{
			  path: '/detail',  //
			  component: detail
			},{
				path: '/search',  //
				component: search
			},{
			  path: '/articles',   //
			  component: articles
			},{
				path: '/h5',   //
				component: h
			},{
				path: '/node',   //
				component: node
			},{
				path: '/mongodb',   //
				component: mongodb
			},{
				path: '/vue',   //
				component: vue
			},{
				path: '/php',   //
				component: php
			}
		]
    },{
		  path: '/login',   //
		  name: 'login',
		  component: login
		},{
		  path: '/manage',  //
		  name: 'manage',
		  component: manage,
		  meta: {
				keepAlive: true // 
			},
			children:[{
				// 
				path:"/pandect",
				name:"pandect",
				component:pandect
			},{
				path: '/addwz',  //
				name: 'add',
				component: add,
				meta: {
					keepAlive: true // 
				}
			},{
				path: '/all',   //
				name: 'all',
				component: all
			},{
				path:"/check",   //
				name:"check",
				component:check
			},{
				path:"/edit",   //
				name:"edit",
				component:edit,
				meta: {
					keepAlive: true // 
				}
			},{
				path:"/hot",   //
				name:"hot",
				component:hot
			},{
				path:"/allDiscuss",   //
				name:"allDiscuss",
				component:allDiscuss
			},{
				path:"/replyCmt",   //
				name:"replyCmt",
				component:replyCmt
			},{
				path:"/noReply",   //
				name:"noReply",
				component:noReply
			}]
		},{
			path: '/test',  //
			component: test
		},{
		// 
			path:"*",
			redirect:"/articles"
		}
  ]
})



