<template>
    <div>
		
		<!-- 
			@  data:
			@  border:
			@  selection-change:,
		 -->
        <el-table :data="article" ref="multipleTable" border height="600" >
			<el-table-column
				prop="timeStr"
				label=""
				sortable
				>
			</el-table-column>
			<el-table-column
				prop="title"
				label=""
				>
			</el-table-column>
			<el-table-column
				prop="writer"
				label=""
				>
			</el-table-column>
			<el-table-column
				prop="content"
				label=""
				>
			</el-table-column>
			<el-table-column
				prop="reply"
				label=""
			>
			</el-table-column>
			<el-table-column
				prop="replyMsg"
				label=""
			>
			</el-table-column>

			<el-table-column
				prop="replyTime"
				label=""
			>
			</el-table-column>

			<!-- 
				filters ,    [{text:,value:}]
				filter-method(val,row)  valfiltersvalue,row ;,boolean
			-->
			<el-table-column
				prop="kind"
				:filters="filt"
				:filter-method="filterTag"
				:filter-multiple="filt_mult"
				label=""
			>
			</el-table-column>
		</el-table>

		<!--  -->
		<div class="deleteArea">
			<fenye class="my_fenye" @fenyeData="pageData(arguments)" :totle="article.length" :defaultPageSize="pageSize"></fenye>
		</div>
    </div>
</template>

<script>
import fenye from '../../client/modules/fenye.vue'
import articleVue from '../../client/article.vue'
import { type } from 'os'
export default{
    name:"replyCmt",
    data(){
        return{
			url:"http://127.0.0.1:3000",
			article:[],
			// 
            filt:[{
				text:"Node.js",
				value:"Node.js"
			},{
				text:"h5+css3",
				value:"h5+css3"
			},{
				text:"PHP",
				value:"PHP"
			},{
				text:"Vue",
				value:"Vue"
			},{
				text:"Mongodb",
				value:"Mongodb"
			},],
			filt_mult:false,    //
			// 
			currentPage:1,
			pageSize:10,
			
			// 
        }
    },
	created () {
		// this.getpl();
		this.getReplyPl();
	},
	components: {
		fenye
	},
	methods: {
		// ,
		pageData(pages){
			this.pageSize=pages[0];
			this.currentPage=pages[1];
		},
		// 
		filterTag(val,row){
			//   valfiltersvalue,
			//   rowvalue;
			return row.kind==val;
		},
		
		// 
		getReplyPl(){
			this.$axios.get(this.url+"/replyPl")
			.then(res=>{
				if(res.data!==0){
					
					this.article=res.data;
					for(let i in this.article){
						if(this.article[i].reply==false){
							this.article[i].reply=""
						}else{
							this.article[i].reply=""
						}
					}
				}else{
					this.article=[];
					return;
				}
			})
			.catch(err=>{
				this.$message({
					message:",",
					type:"error",
					durations:1000,
				})
			})
		},
	},
	
}
</script>

<style scoped="scoped">
	/deep/.el-table td{
		text-align: center;
	}
	/deep/.el-table th{
		text-align: center;
	}
	/deep/ .el-table .cell{
		white-space: nowrap;
		line-clamp: 1;
		-webkit-line-clamp:1;
		overflow: hidden;
		text-overflow: ellipsis
	}
	/deep/ .rowStyle{
		background-color: blue;
	}

	.deleteArea{
		width: 100%;
		display: flex;
		justify-content: space-between
	}
	.deleteArea .my_button{
		flex:1
	}
	.deleteArea .my_fenye{
		flex:9
	}
	.my_fenye{
		text-align: right
	}

	/deep/ .el-form-item--mini textarea{
		resize: none
	}
</style>


