<template>
    <div id="main" >
      <!--  -->
      <el-row>
        <el-col :span="12">
          <div id="classify" style="height:500px"></div>
        </el-col>

        <el-col :span="12">
          <div id="DailyVisits" style="height:500px"></div>
        </el-col>
			
      </el-row>
    </div>
</template>

<script>
import echarts from 'echarts'
export default {
    name:"pandect",
    data(){
        return{
			url:"http://127.0.0.1:3000",
			pie: '',
			lines:"",
						// 
            classifys:{
				readNum:0,
							// 
			    opinion:[],
			    opinionData:[]
            },
			
						// 
			DailyVisit:{
				xData:["2020-2-14","2020-2-15","2020-2-16"], //x
				readSum:[10,15,20],
			},
			amount:0,			
        }
    },
	created() {
		this.getReadNum();
		
	},
    mounted () {
		this.visit()
	},
    methods: {
		// 
			szqc:function(arr){
				for(var i=0; i<arr.length; i++){
					for(var j=i+1; j<arr.length; j++){
						if(arr[i].kind==arr[j].kind){         //splice
							arr[i].reading+=arr[j].reading
							arr.splice(j,1);
							j--;
						}
					}
				}
				return arr;
			},

			szqc2:function(arr){
				for(var i=0; i<arr.length; i++){
					for(var j=i+1; j<arr.length; j++){
						if(arr[i].readDate==arr[j].readDate){         //splice
							arr[i].readSum+=arr[j].readSum
							arr.splice(j,1);
							j--;
						}
					}
				}
				return arr;
			},

			// 
			getReadNum(){
				this.$axios.get(this.url+"/getReadNum")
				.then(res=>{
					// 
					if(res.data){
						
						for(let i in res.data){
							// 
							this.classifys.readNum+=res.data[i].reading;
							
							if(res.data[i].reading!==""&&res.data[i].reading>0){
								// valuename,
								res.data[i].name=this.szqc(res.data)[i].kind;
								res.data[i].value=this.szqc(res.data)[i].reading;
							}
							// 
							this.classifys.opinionData=this.classifys.opinionData.concat(res.data[i]);
						}
						
						// 
						this.readPie(this.classifys.opinionData)
						
					}else{
						// 
						this.classifys.opinionData[0].name="vue";
						this.classifys.opinionData[0].value="0";
						this.readPie(this.classifys.opinionData);
					}
				})
				.catch(err=>{
					console.log(err)
				})
			},
			
			// 
			readPie(data){
					this.pie = echarts.init(document.getElementById('classify'));
						this.pie.setOption({
							title:{
								text:"",
								left: 'center'
							},
								// 
							tooltip: {
							  trigger: 'item',
							  alwaysShowContent:true,
							  formatter: "{b}:{d}%"	
							},

							// 
							legend: {   
								orient: 'vertical',
								x: 'left',
									//  name
								// data:data.name
							},
							
							series: [
								{	
									top:0,
									hoverOffset :1,
									type:'pie',
									radius:['0%','50%'],
									avoidLabelOverlap: false,
									data:data,
									// labelLine :{show:true,smooth:true},
									itemStyle:{ 
										normal:{ 
											label:{ 
												show: true, 
												formatter: "{b}:{c}"
												
											}, 	
										} 
									}
								},
							]
						})
			},

			// 
			visit:function(){
				let today=this.dateStr();
				
				this.$axios.get(this.url+"/getVisited",{params:{
					today:today
				}})
				.then(res=>{
					
					if(res.data){
						
						let todayDate=this.szqc2(res.data);

						 for( let i in todayDate ){
							 this.DailyVisit.xData.push(todayDate[i].readDate);
							 
							 this.DailyVisit.readSum.push(todayDate[i].readSum)
							 this.visitLine(this.DailyVisit.xData,this.DailyVisit.readSum)
						 }
						
					}

				})
				.catch(err=>{
				})
			},

			// 
			visitLine:function(xDate,serieDate){
				let sum=0;
				for(let i in serieDate){
					sum+=serieDate[i]
				}
				this.lines=echarts.init(document.getElementById("DailyVisits"));
				this.lines.setOption({
					title:{
						text:"",
						left: 'center',
						subtext:""+sum+"",
						// 
						subtextStyle:{
							fontSize:12,
							textStyle:'middle'
						}
					},
					
					tooltip: {
						trigger: 'item',
						alwaysShowContent:true,
						formatter:"{b}<br/>:<span style=color:#00B7F9>{c}</span>"
					},

					legend:{
						show:true,
						y:'bottom',
						x:'center',
					},
					xAxis:[{
						type: 'category',
						data:xDate,
						axisTick:{
							show:true,   //,true
                			alignWithLabel:true,
						}
					}],
					yAxis:{
						type:'value',
						
					},
					dataZoom:[{
						type:'inside',   //,
						xAxisIndex:0,   //,
						yAxisIndex:0,
						// filterMode:'filter', //   
						// filterMode:'weakFilter',     //   
						// filterMode:'empty',   //   
						filterMode:'none',//,
						
					}],
					series:[{
						type:'line',
						// name:"",
						data:serieDate,
						itemStyle:{ 
							normal:{ 
								label:{ 
									show: true, 
									formatter: "{c}"
								}, 	
							} 
						}
					}]
				})
			}
    }
}
</script>>

<style scoped>

</style>


