<!-- 
    ,idid;

-->
<template>
    <div style="width: 50%;">
         <header>
            <router-link to="/all"></router-link> >>> 
        </header>

        <el-form label-width="80px" :model="form" ref="form" class="demo-ruleForm" >
            <el-form-item label="" >
                <el-input v-model="form.title"></el-input>
            </el-form-item>
              <el-form-item label="" >
                <el-input v-model="form.kind" readonly></el-input>
            </el-form-item>
            <el-form-item label="">
               <!--  -->
                <editor ref="checkDatas" @editorData="getEditorData(arguments)"></editor>
            </el-form-item>
            <el-form-item label="">
                <el-input type="text" v-model="form.writer"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="alter()"></el-button>
                <el-button type="infor" @click="back()"></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>
<script>
import editor from '../../repeatModule/editor'
export default{
    name:"edit",
    data(){
        return{
            queryId:'',
            form:{},
            url:"http://127.0.0.1:3000",
           
        }
    },
    components: {
        editor
    },
    created () {
       this.getRoutId();
    },
    methods: {
        // 
		getEditorData(data){
			this.form.content=data[0];
			
		},

        // Idid. created() 
        getRoutId(){
           this.queryId=this.$route.query.id;  //Id
           this.$axios.get(this.url+"/editData",{params:{
               id:this.queryId
           }})
           .then(res=>{
              
               if(res.data){
                   this.form=res.data;
                   // 
                    this.$refs.checkDatas.checkData(this.form.content,true);
               }
           })
           .catch(err=>{
                this.$message({
                    message:err.message,
                    type:"error",
                    durations:1000,
                })
           })
        },
        // 
        back(){
            this.$router.go(-1)
        },

        // 
        alter(){
           
            //, 1 

            this.$axios.get(this.url+"/alter",{params:{    
                id:this.queryId,  //id
                title:this.form.title,  //
                content:this.form.content,  //
                writer:this.form.writer, //
                date:this.dateStr(),  //
            }})
            .then(res=>{
               if(res.data=="1"){
                   this.$message({
                        message:"",
                        type:"success",
                        durations:1500,
                    })
                    this.back()   //
               }else{
                    this.$message({
                        message:",",
                        type:"error",
                        durations:1500,
                    })
               }
            })
            .catch(err=>{
                this.$message({
                    message:"",
                    type:"error",
                    durations:1500,
                })
            })
        }
    }
}
</script>
<style scoped>
    header{
        padding: 0 0 20px;
        font-size: 14px
    }
    header a{
        color: #409EFF
    }
   .el-textarea .el-textarea__inner{
       resize: none;
   }
    /deep/ .ql-container{
		height: 500px;
	}
</style> 


