<template>
    <div>
		
		<!-- 
			@  data:
			@  border:
			@  selection-change:,
		 -->
        <el-table :data="article" ref="multipleTable" border height="600" >
			<el-table-column
				prop="timeStr"
				label=""
				sortable
				>
			</el-table-column>
			<el-table-column
				prop="title"
				label=""
				>
			</el-table-column>
			<el-table-column
				prop="writer"
				label=""
				>
			</el-table-column>
			<el-table-column
				prop="content"
				label=""
				>
			</el-table-column>
			<el-table-column
				prop="reply"
				label=""
			>
			</el-table-column>

			<!-- 
				filters ,    [{text:,value:}]
				filter-method(val,row)  valfiltersvalue,row ;,boolean
			-->
			<el-table-column
				prop="kind"
				:filters="filt"
				:filter-method="filterTag"
				:filter-multiple="filt_mult"
				label=""
			>
			</el-table-column>

           <el-table-column label="">
				<template slot-scope="scope">
					<el-button type="text" size="small" @click="reply(scope.row)"></el-button><!-- scope.rowid,slot-scope="scope" -->
				</template>
			</el-table-column>
		</el-table>
		 <!--  -->
            <el-dialog :title="':'+aboutReply.commentTtile" :visible.sync="aboutReply.commentArea" :close-on-click-modal='false'>
                <el-form class="my_cmt_form" size="mini">								
                    <el-form-item label=":" size="mini">  
                        <el-input type="textarea" readonly  :placeholder="aboutReply.commentPlaceHolder" :autosize={minRows:3,maxRows:6}></el-input>
                    </el-form-item>
                </el-form>

                <el-form class="my_cmt_form" size="mini">
                    <el-form-item label=":" size="mini"> 
                        <el-input type="textarea" placeholder="" v-model="aboutReply.replyMsg" :autosize={minRows:3,maxRows:6}></el-input>
                    </el-form-item>
                </el-form>

                <div slot="footer" style="text-align:right">
                    <el-button type="primary" title="" :disabled="aboutReply.sure" @click="sureReply()"></el-button>
                    <el-button @click="replyReset()"></el-button>
                </div>
            </el-dialog>
			
		<!--  -->
		<div class="deleteArea">
			<fenye class="my_fenye" @fenyeData="pageData(arguments)" :totle="article.length" :defaultPageSize="pageSize"></fenye>
		</div>
		
		
    </div>
</template>

<script>
import fenye from '../../client/modules/fenye.vue'
import articleVue from '../../client/article.vue'
import { type } from 'os'
export default{
    name:"noReply",
    data(){
        return{
			url:"http://127.0.0.1:3000",
			article:[],
			// 
            filt:[{
				text:"Node.js",
				value:"Node.js"
			},{
				text:"h5+css3",
				value:"h5+css3"
			},{
				text:"PHP",
				value:"PHP"
			},{
				text:"Vue",
				value:"Vue"
			},{
				text:"Mongodb",
				value:"Mongodb"
			},],
			filt_mult:false,    //
			// 
			currentPage:1,
			pageSize:10,
			
			
            
            // 
			aboutReply:{
				commentArea:false,    //
				commentTtile:"",  //
				commentPlaceHolder:"",  //,
				replyObj:{},   //
				replyMsg:"", //
				sure:true,  //
			},
        }
    },
	created () {
		
		this.getpl();
	},
	components: {
		fenye
	},
	methods: {
		// ,
		pageData(pages){
			this.pageSize=pages[0];
			this.currentPage=pages[1];
		},
		// 
		filterTag(val,row){
			//   valfiltersvalue,
			//   rowvalue;
			return row.kind==val;
		},
		
		// 
		getpl(){
			this.$axios.get(this.url+"/noReply")
			.then(res=>{
				this.article=res.data;
				
				for(let i in this.article){
					
					if(this.article[i].reply==false){
						this.article[i].reply=""
					}else{
						this.article[i].reply=""
					}
				}
			})
			.catch(err=>{
				
			})
		},
        

        // 
		reply(obj){
			this.aboutReply.commentArea=true;  //
			this.aboutReply.sure=true;
			this.aboutReply.replyObj=obj;  //,,
			
			this.aboutReply.commentTtile=this.aboutReply.replyObj.title;   //
			this.aboutReply.commentPlaceHolder=this.aboutReply.replyObj.content   //
        },
		
		
		// 
		sureReply(){
			//  ,
			//   1
		
			if(this.aboutReply.replyMsg!==""){
				
				this.$axios.get( this.url+"/getReply",{ 
					// 
						// titleId   id
						// dateStr   
						// commentId id
						// writer    
						// replyMsg  
					params:{
						
						titleId:this.aboutReply.replyObj.titleId,   
						dateStr:this.dateStr(),
						commentId:this.aboutReply.replyObj._id,
						writer:this.aboutReply.replyObj.writer,
						replyMsg:this.aboutReply.replyMsg,
					}
				})
				.then(res=>{
					
					if(res.data==1){
						this.aboutReply.sure=true;   // '' 
						var that=this;
						this.getpl();
						that.$message({
							message:"",
							durations:500,
							type:"success",
							onClose:function(){
								that.aboutReply.commentArea=false;
							},
						})
					}
				})
				.catch(err=>{
					that.$message({
						message:"",
						durations:500,
						type:"error",
						onClose:function(){
							that.aboutReply.commentArea=false;
						},
					})
				})
			}
		},

        
    },
	
	
	watch: {
		'aboutReply.replyMsg':{
			handler(n,o){
				
				if(n!==""){
					
					this.aboutReply.sure=false;
				}else{
					this.aboutReply.sure=true;
				}
				
			},
			// deep:true
		},
		'aboutReply.commentArea':{
			
			handler(n,o){
				
				if(n==true){
					this.aboutReply.replyMsg="";

				}
			}
		}
	}
 
}
</script>

<style scoped="scoped">
	/deep/.el-table td{
		text-align: center;
	}
	/deep/.el-table th{
		text-align: center;
	}
	/deep/ .el-table .cell{
		white-space: nowrap;
		line-clamp: 1;
		-webkit-line-clamp:1;
		overflow: hidden;
		text-overflow: ellipsis
	}
	/deep/ .rowStyle{
		background-color: blue;
	}

	.deleteArea{
		width: 100%;
		display: flex;
		justify-content: space-between
	}
	.deleteArea .my_button{
		flex:1
	}
	.deleteArea .my_fenye{
		flex:9
	}
	.my_fenye{
		text-align: right
	}

	/deep/ .el-form-item--mini textarea{
		resize: none
	}
</style>


