<!--  -->
<template>
	<div class="box">

		<!--  -->
		<header>
			<div>
				<h2></h2>
				<p>express+mongoose!!!</p>
			</div>
			<div>

				<router-link to="/login"></router-link>
			</div>
		</header>

		<!-- <navList></navList> -->
		<nav>
			<ul>
				<li v-for="(nav,index) in navList" @click="toType(nav.path,index)"><!-- :class="{'changeColor':col==index}" -->

					{{nav.nav}}
          
				</li>

			</ul>
		</nav>


		<!--  -->
		<div class="main">
			<!--  -->
			<article>
				<router-view ></router-view>
			</article>

			<!--  -->
			<!-- sendSearchData -->
			<aside>
				<!--  -->
				<sidebar></sidebar>
			</aside>


		</div>

	</div>
</template>

<script>


import sidebar from "./modules/aside.vue"   //
export default{
	name:"index",
	data(){
		return{
			// currentPage:1,
			// pageSizes:[5,10,15],
			// pageSize:5,
			url:"http://127.0.0.1:3000",
			navList:[{
                    nav:"",
					path:'/'
                },{
                    nav:"h5+css3",
					path:'/h5',
					name:"h5+css3",
                },{
                    nav:"Node.js",
					path:'/node',
					name:"Node.js"
                },{
                    nav:"Vue",
					path:'/vue',
					name:"Vue"
                },{
                    nav:"MongoDB",
					path:'/mongodb',
					name:"Mongodb"
                },{
                    nav:"PHP",
					path:'/php',
					name:"PHP"
				}
			],
			articles:[],
			searchDatas:[],
			col:0,
		}
	},
	components: {
		sidebar,
		// fenye
	},
	created() {
		this.$router.push({
			path:"/articles"
		})
	},
	methods:{
		// 
		// ele,index
		toType(ele,index){
			this.col=index;
			// ,
			if(ele=="/"){
				this.$router.push({
					path:"/index"
				})
				return;
			}
			// ,

			this.$router.push({
				path:ele,
				query:{
					type:this.navList[index].name
				}
			})
		},


	}
}
</script>

<style scoped="scoped">


	.box{
		min-height:100%;

	}
	/*  */
	header{
		width: 100%;

		display: flex;
		justify-content: space-between;
	}
	header div:nth-child(2){
		margin-right: 30px;
		font-size: 14px;
	}
	header div:nth-child(2) a{
		color: #6A6A6A;
	}

	header h2{
		color: #bbb;
	}
	header p{
		margin-top: 10px;
		font-size: 14px;
	}

	/*  */
	nav{
		width: 100%;
		margin-top: 30px;
		padding: 0 10px;
		border-top: 1px solid #eee;
		border-bottom: 1px solid #eee;
	}
	nav ul{
		display: flex;
		justify-content: space-around
	}
	nav ul li{
		font-size: 14px;
		line-height: 35px;
		cursor: pointer;
		text-align: center;
		width: 16.66%
	}
	nav ul a{
		color: #6a6a6a;
		/* padding: 10px 50px; */
		cursor: pointer;
	}
	nav ul li:hover{
		color: #00B7FF;
	}
	.changeColor{
        color: #00B7FF;
    }


	/*  */
	.main{
		width: 100%;
		display: flex;
		justify-content: space-between;
		/* margin-top: 15px; */
	}
	article{
		width: 70%;
		padding: 10px 0;
	}


	/*  */
	aside{
		width: 20%;
		padding: 10px 0;
	}




	/*  */
	footer{
		width: 100%;
		padding-top: 20px;
		text-align: center;
		font-size: 14px;
		/* position: fixed;
		bottom: 100px; */

	}
</style>



