<template>
        <div>
            <div class="seach">
                <div>
                    <input type="text" placeholder=""  v-model="searchMsg" v-on:keyup.enter="searchBtn()" @blur="searchBlur()">
                    <button @click="searchBtn()"></button>
                </div>
                <ul v-show="search_keydown">
                    <li v-for="(item,index) in searchResult" :title="item.title" ref="liRef" @mouseout="selectOut()" @mouseover="selectOver()"  @click="selectLi(index)">
                        {{item.title}}
                    </li>
                </ul>
            </div>
            
            <div class="type hot">
                <dl>
                    <dt></dt>
                    <dd v-if="!zanwu" class="zanwu">
                        <span>!</span>
                    </dd>
                    <dd v-for="(hotItem,index) in hotArticle" @click="getId(hotArticle,index)">
                        <span>[{{index+1}}]</span>
                        <a :title="hotItem.title" :class="{'changeColor':col==index}">{{hotItem.title}}</a>
                    </dd>
                   
                </dl>
            </div>
            <div class="type update">
                <dl>
                    <dt></dt>

                    <dd v-for="(near,index) in nearest" @click="getId(nearest,index)">
                        <span>[{{index+1}}]</span>
                        <a :title="near.title" >{{near.title}}</a>
                    </dd>
                </dl>
            </div>
           
        </div>
    

</template>
<script>
export default{
    name:"sidebar",
    data(){
        return{
            mydata:[],
            hotArticle:[],    //
            nearest:[],         //
            tymeMe:[],
            url:"http://127.0.0.1:3000",
            zanwu:false,
            searchMsg:"",
            searchResult:[],
            search_keydown:false,
            liRef:false,   //li
            col:0,
        }
    },
    created () {
        this.hot();
        this.recent_updates();
    },
   
    methods: {
        hot(){
			this.$axios.get(this.url+"/look")
			.then(res=>{
                if(res.data.length>0){
                    this.mydata=res.data;
                    
                    this.hotArticle=res.data;
                   
                    this.zanwu=true;
                        // 
                    this.hotArticle.sort(function(a,b){
                        // 
                        return b.reading-a.reading;
                    })
                    if(this.hotArticle.length>5){
                        this.hotArticle=this.hotArticle.slice(0,5);
                    }
                }else{
                    this.zanwu=false;
                }
			})
			.catch(err=>{
				console.log(err)
			})
        },
        
        // 

        recent_updates(){
            this.$axios.get(this.url+"/look")
            .then(res=>{
                if(res.data.length>0){
                    this.nearest=res.data;
                    for(let i in this.nearest){
                       this.nearest[i].date=this.nearest[i].date.replace(/-/g,'/');
                    }
                    if(this.nearest.length>5){
                        this.nearest=this.nearest.slice(0,5);
                    }
                }
            })
        },

        // ,id
		getId(arr,index){
           
            this.col=index;
			this.$router.push({
				path:"/detail",
				query:{
					id:arr[index]._id
				}
            })
          
        },


        // 1.
        searchBlur(){
            // console.log(this.searchMsg,this.liRef)
            // (li),
            if(this.liRef==false){
                this.search_keydown=false;
            }
        },

        //  
        // 
        //   lilivuex
        //   
        //   
        selectLi(i){
            // li
            if(this.$refs.liRef[0].tagName=="LI"){
                this.liRef=true;    //
                // vuex
                this.$store.dispatch("toGetSearch",this.searchResult[i]);   
                this.search_keydown=false;//
                // 
                this.$router.push({
                    path:"/search"
                })

            }
        },
        // 
        selectOver(){
            this.liRef=true;  //li
        },
        // 
        selectOut(){
            this.liRef=false;   //li
        },

        //  :
        //     ;
        //     vuex
        //     
        //     vuex
        searchBtn(){
            if(this.searchMsg!==""){
                if(this.searchMsg.trim().length==0){
                    this.searchMsg="";
                    return false
                }
                // vuex
                this.$store.dispatch("toGetSearch",this.searchResult);
            
                this.search_keydown=false;
                this.$router.push({
                    path:"/search"
                })
            }
        }
    },
    watch: {
        // ,

        "searchMsg":function(n,old){
        //   
            if(n!==""&&n.trim().length>0){
                
                // this.searchResult=[];
                
                this.searchResult=this.mydata.filter((item,index,arr)=>{
                    // 
                    return arr[index].title.includes(this.searchMsg);
                     
                })
                if(this.searchResult.length<=0){
                    this.search_keydown=false;
                }else{
                     this.search_keydown=true;
                }
            }else{
                
                this.searchResult=[];
                this.search_keydown=false;
                this.$router.push({
                    path:"/index",

                })
            }
        }
    }
   
}
</script>
<style scoped>
    aside .seach{
        position: relative;
    }
    aside .seach ul{
        position: absolute;
        width: 60%;
        left: 0;
        font-size: 14px;
        border-left: 1px solid #eee;
        border-right: 1px solid #eee;
        padding-top: 10px;
        z-index: 50;
        background: #fff;
    }
    aside .seach ul li{
        overflow: hidden;
        
        -webkit-line-clamp: 1;
        text-overflow: ellipsis;
        white-space: nowrap;
        padding: 5px;
        border-bottom: 1px solid #eee;
    }
    aside .seach ul li:hover{
        background-color: rgb(236, 231, 231);
    }
    aside .seach input{
		padding: 5px;
		border:1px solid #E3E3E3;
		border-radius: 3px;
        width: 60%;
		outline: none;
	}
	aside .seach button{
		padding: 5px 10px;
		background-color:#00B7FF;
		border: 1px solid #00B7FF;
		border: none;
		margin: 0;
		color: #fff;
		border-radius: 3px;
		cursor: pointer;
		outline: none;
	}
	/* aside .type{
		margin-top: 40px;
	} */
	aside .type dt{
		font-weight: 600;
	}
	aside .type dd{
		font-size: 14px;
        padding-left:5px;
        margin-top: 10px;
		color: #6A6A6A;
        overflow:hidden;
		text-overflow:ellipsis;
		display:-webkit-box;
		-webkit-box-orient:vertical;
		-webkit-line-clamp:1;
		
		white-space:pre-line;
        
	}
	aside .type dd a{
		text-decoration:underline;
		color: #6A6A6A;
        cursor: pointer;
	}
	aside .type dd a:hover{
		color:#00B7FF;
	}
    .changeColor{
        text-decoration: underline;
    }
</style>


