



<!-- 
	1:  ,
	2:  

 -->
<template>
	<div style="width: 70%;">
		
		<el-form label-width="80px" :rules="rules" :model="form" ref="form" class="demo-ruleForm">
			<el-form-item label="" prop="title">
				<el-input v-model="form.title" class="my_inp">
				</el-input>
			</el-form-item>
			<el-form-item label="" prop="kind">
				<el-select v-model="form.kind" placeholder="">
				  <el-option label="PHP" value="PHP"></el-option>
				  <el-option label="Node.js" value="Node.js"></el-option>
				  <el-option label="h5+css3" value="h5+css3"></el-option>
				  <el-option label="Vue" value="Vue"></el-option>
				  <el-option label="Mongodb" value="Mongodb"></el-option>
				</el-select>
			</el-form-item>

			<el-form-item label="" prop="content">
				<!--  
					@editorData
					ref;
					ps:  :child="data"
				-->

				<editor @editorData="getEditorData(arguments)" v-model="form.content" ref="clears" ></editor>	
				
			</el-form-item>

			<el-form-item label="" prop="writer" class="my_inp">
				<el-input type="text" placeholder=":" v-model="form.writer"></el-input>
			</el-form-item>
			<el-form-item>
				<el-button type="primary" @click="onSure('form')"></el-button>
				<el-button @click="onReset('form')"></el-button>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>

import editor from '../repeatModule/editor'
import QS from 'qs'

export default({
	name:"add",
	data(){
		return{
			flag:true,
			url:"http://127.0.0.1:3000",
			form:{
				title:"",
				kind:"",
				content:"",
				writer:"",
			},
			rules:{
				title:[{
					required: true, message: '', trigger: 'blur'
				}],
				kind:[{
					required:true,message:"",trigger:'change'
				}],
				content:[{
					required:true,message:"",trigger:'blur'
				}]
			},
			
			
		}
	},
	components: {
		editor	
	},
	
	mounted () {
			
	},
	methods:{
		// 
		getEditorData(data){
			this.form.content=data[0];
			
		},
		
		// 
		onSure(form){
			// , valid=true
			
			this.$refs[form].validate((valid) => {
				if (valid){
					if(this.form.content){
						this.$axios.post(this.url+"/getAdd",QS.stringify({
							content:this.form.content,
							title:this.form.title,
							kind:this.form.kind,
							writer:this.form.writer,
							date:this.dateStr(),
						}))
						.then(res=>{
							// 
							// 1
							if(res.data==1){
								this.$message({
									message:"",
									type:"success",
									durations:1500,
								})
								this.$refs[form].resetFields();   //
								// editorClear()
								// :
								//     ref,clears
								//     this.$refs.clears.() 
								this.$refs.clears.editorClear(true)

								this.$route.meta.keepAlive=false;
							}else{
								this.$message({
									message:"",
									type:"error",
									durations:1500,
								})
								
							}
						})
						.catch(err=>{
							this.$message({
								message:err.message,
								type:"error",
								durations:1500,
							})
						})
					} else {
						// 
						return false;
					}
				}
			})
		},

		// 
		onReset(form){
			this.$refs[form].resetFields();
			// editorClear()
			// :
			//     ref,clears
			//     this.$refs.clears.() 
			this.$refs.clears.editorClear(true);

		}
	}
})
</script>

<style scoped>

	.el-form-item__content{
		line-height: 30px;
	}

	.my_inp input{
		width: 50%
	}

	.editor{
		
		border: 1px solid #DCDFE6;
	}
</style>



