import Vue from 'vue'
import Vuex from 'Vuex'

Vue.use(Vuex);
export default new  Vuex.Store({
    state:{
        searchData:[]
    },
    // 
    mutations:{
        getSearch(state,data){
            state.searchData=[];
            state.searchData=state.searchData.concat(data);
           
        }
    },

    // ,mutationsstate
    actions:{
        toGetSearch(context,searchData){
            context.commit("getSearch",searchData);
            
        }
    }
})


