<!--  -->
<template>
	<div style="padding-bottom:10px">
		<!--  -->

		<div class="comment">
			<div class="comment_title">
				<h4></h4>
				<span>
					{{cmnts.length}}
				</span>
			</div>
			<div class="comment_inp">
				<el-input @focus="showDialog()" type="textarea" readonly  placeholder="5" :autosize="{minRows:1,maxRows:1}"></el-input>
			</div>
		</div>

		<el-dialog :title="':'+dialogTitle" :visible.sync="commentArea" :close-on-click-modal='false' width="30%">
												 <!-- model -->
			<el-form ref="form" class="my_cmt_form" :model="form" size="mini" :rules="rules">
												<!--propmodel-->
				<el-form-item label=":" size="mini" prop="text">  
					<el-input type="textarea" v-model="form.text" placeholder="" :autosize="{minRows:3,maxRows:3}"></el-input>
				</el-form-item>
				<el-form-item style="text-align:right">
					<el-button type="primary" @click="talk('form')" title="" :disabled="form.text.length>0?disabled:!disabled" ></el-button>
					<el-button @click="reset('form')" :disabled="form.text.length>0?disabled:!disabled"></el-button>
				</el-form-item>
			</el-form>
		</el-dialog>


		<!--  -->
		<div class="article" v-for="(article,index) in articles" :key="index">
			<h3>{{article.title}}</h3>
			<p class="content" v-html="article.content">{{article.content}}</p>
			<div class="time">
				<span>--{{article.date}}</span>
				<span>--{{article.writer}}</span>
				<span>--({{article.kind}})</span>
			</div>
		</div>
		
	<!--  -->
		
		<div v-if="!isCmnts" style="text-align:center;font-size:14px;color:#b1b1b1;margin-top:20px">
			{{noComment}}
		</div>
		<!--  -->
		<div class="talk" v-for="cmt in cmnts.slice( (currentPage-1)*pageSize,pageSize*currentPage )">
			
			<header>
				<div class="talk_img">
					<img src="../../assets/tx.jpg" alt="">
				</div>
				<div class="talk_name">
					<p style="color: #444;"></p>
					<p style="color: #666;">{{cmt.timeStr}}</p>
				</div>
			</header>
			<div>
				<span>{{cmt.content}}</span>
			</div>
			
		</div>
		<fenye class="fenye" :totle="cmnts.length" :defaultPageSize="pageSize" @fenyeData="pagesData(arguments)"></fenye>
	</div>
</template>

<script>
import fenye from './modules/fenye.vue'
export default{
	name:"detail",
	data(){
		return{
			pageSize:5,
			currentPage:1,
			noComment:"",
			id:'',   //id
			url:"http://127.0.0.1:3000",    //
			articles:[],   //,id
			form:{   //
				text:""
			},
			rules:{
				text:[{min:5,message: '5',}]
			},
			disabled:false,
			cmnts:[],   //
			isCmnts:false,   //
			msgDiabled:false,
			dialogTitle:"",
			commentArea:false
		}
	},
	components: {
		fenye	
	},
	created() {
		// 
		this.getDetail();
	
		// 
		this.getComments();
	},
	
	methods: {

		
		// 
		showDialog(){
			this.commentArea=true;
			
		},

		// ,id
		getComments(){
			this.cmnts=[],
			this.$axios.get(this.url+"/comments",{params:{
				titleId:this.id,

			}})
			.then(res=>{
				// ,
				for( let i in res.data ){
					if( res.data[i].isShow==true ){
						this.cmnts.push(res.data[i]);
					}
					// console.log(this.cmnts)
				}
				
				if(this.cmnts.length>0){
					this.isCmnts=true;
				}else{
					this.isCmnts=false;
				}
			})
		},

		// id,
		getDetail(){
			this.id=this.$route.query.id;  //id
			this.articles=[];  //
			this.$axios.get(this.url+"/details",{params:{   
				id:this.id,
				readDate:this.dateStr(),  //
			}})
			.then(res=>{
				// 
				if(res.data!==0){
					this.articles.push(res.data);//
					// console.log(res.data);
					this.dialogTitle=this.articles[0].title;
				}else{
					// 
					this.noComment="";
					this.msgDiabled=true;
				}
			})
			.catch(err=>{
				this.$message({
					message:""+err,
					durations:1000,
					type:"warning"
				})
			})
		},

		// 
		talk(form){
			
			this.commentArea=false;
			this.$refs[form].validate(val=>{
				//  
				if(val&&this.form.text.trim().length>0){
					let that=this;
				// 
					that.$axios.all([this.tjpl(),this.hqpl()])
					.then(that.$axios.spread(function(tj,hq){
						if(hq.data){
							//
							that.cmnts=hq.data;
							that.isCmnts=true;
							// 
							that.$refs[form].resetFields()
						}
					}))
				}else{
					this.$message({
						message:"",
						type:"warning",
						durations:1000,
					})
				}
			})
		},
		
		// 
		tjpl(){
			return this.$axios.get(this.url+"/pl",{params:{
				titleId:this.id,   //id
				title:this.articles[0].title,   //
				writer:this.articles[0].writer,  //
				time:this.dateStr(),   //
				content:this.form.text,  //
				kind:this.articles[0].kind
			}})
		},

		// ;
		hqpl(){
			return this.$axios.get(this.url+"/comments",{params:{
				titleId:this.id
			}})
		},

		// 
		reset(form){
			this.$refs[form].resetFields();
		},

		// 
		pagesData(pages){
			this.pageSize=pages[0];
			this.currentPage=pages[1];
		}
	},

	watch: {
		"$route"(to,from){
			if(to.query.id!==from.query.id){
				this.getDetail();
				this.getComments();
			}
		},
	},
	
}
</script>

<style>
	
	
	.article{
		padding: 10px 0;
		border-bottom: 1px solid #eee;
	}
	.article h3{
		font-weight: 500;
		color:#00B7FF;
		cursor: pointer;
		display: inline;
	}
	.article h3:hover{
		color: #000
	}
	.article>.content{
		padding:10px 15px;
		font-size: 16px;
		color: #666;
		line-height: 25px;
		white-space:pre-line;
		
	}
	
	.article .time{
		margin-top: 10px;
		font-size: 12px;
		color: rgb(138, 135, 135);
	}

	/*  */
	.talk{	
		border-bottom: 1px solid #eee;
		margin-top: 40px;
		padding: 10px 0;
	}
	.talk header{
		display: flex;
		flex-direction: row;
		justify-content: start;
		align-items: center;
	}
	.talk header .talk_img{
		width: 35px;
		height: 35px;
		
	}
	.talk header .talk_img img{
		width: 100%;
		height: 100%;
		border-radius: 50%
	}
	
	.talk_name p{
		font-size: 12px;
		margin-left: 15px
	}
	.talk>div{
		padding-left: 50px;
		font-size: 14px;
		margin-top: 15px
	}
	.talk_time{
		display: flex;
		justify-content: space-between
	}
	.talk_time dd{
		color: #666;
		margin-left: 15px
	}
	
	.el-textarea .el-textarea__inner{
		resize: none;
	}

	/*  */
	.comment{
		width:100%;
		border-bottom: 1px solid #eee;
		padding-bottom: 10px
	}
	.comment_title{
		display: flex;
		align-items: baseline;
		padding-bottom: 10px
	}
	.comment_title h4{
		margin-right:1%
	}
	.comment_title span{
		font-size: 12px;
		
		color: #b1b1b1
	}
	.comment_inp {
		padding: 10px 15px;
		background-color: #eee

	}
	.comment_inp textarea{
		min-height: 30px !important;
		height: 30px !important;
		line-height: 1.3;
	}


	/* .comment .my_cmt_form{
		border: 1px solid #eee;
		padding: 15px;
		width: 35%;
		margin-left: 25%
	} */


	.ql-syntax{
		background-color: #23241f;
		color: #f8f8f2;
		overflow: visible;
		line-height: 20px
	}
	pre{
		white-space:pre-wrap;
		margin-bottom:5px;
		margin-top:5px;
		padding:5px 10px
	}

	footer{
		display: none;
	}
</style>



