<template>
<!--  -->
    <div>
        <div class="editor" v-highlight>
            <div ref="toolbar" v-highlight class="toolbar">
            </div>
            <div ref="editor" v-highlight class="text">
            </div>
        </div>
    </div>
</template>

<script>
import E from 'wangeditor'
// 
import hljs from 'highlight.js'
import 'highlight.js/styles/dark.css'

export default{
    name:"editor",
    
    data(){
        return{
             // 
            editor:null,
            // 
            content:"",
            url:"http://127.0.0.1:3000"
        }
       
    },
    directives: {
      "highlight":function(el){
            let blocks = el.querySelectorAll('pre code');
            blocks.forEach((block)=>{
                hljs.highlightBlock(block)
            })
      }
    },
    mounted () {
        // 
        
        this.setEditor();
      
        this.editor.txt.html(this.content);
        
    },
    methods: {

        // wangEditor
        setEditor(){
            
            // editor
            this.editor = new E(this.$refs.toolbar, this.$refs.editor)
            this.editor.customConfig.showLinkImg = false // tab 
            
            this.editor.customConfig.uploadImgShowBase64 = false // base 64 
            this.editor.customConfig.uploadImgServer = "http://127.0.0.1:3000/getImg";// 
          
            this.editor.customConfig.uploadFileName = 'img' // 
            this.editor.customConfig.uploadImgMaxSize = 2 * 1024 * 1024 //  2M
            this.editor.customConfig.uploadImgMaxLength = 3 //  3 
            this.editor.customConfig.uploadImgTimeout = 3 * 60 * 1000 // 
            
            this.editor.customConfig.pasteTextHandle = function (content) {
                // content html  
              
                return content 
            }
           
             // 
            this.editor.customConfig.menus = [
                'head', // 
                'bold', // 
                'fontSize', // 
                'fontName', // 
                'italic', // 
                'underline', // 
                'list',  //
                'justify', // 
                'strikeThrough', // 
                'foreColor', // 
                'backColor', // 
                'link', // 
                'emoticon', // 
                'image', // 
                // 'code', // 
                
			],
			// 
            this.editor.customConfig.uploadImgHooks = {
				before: function (xhr, editor, files) {
					// 
					// xhr  XMLHttpRequst editor files 
					
					//  {prevent: true, msg: 'xxxx'} 
					// return {
					//     prevent: true,
					//     msg: ''
					// }
					// console.log(files,editor)
				},
				fail: (xhr, editor, result) => {
					// 
				},
              // 
				success: (xhr, editor, result) => {
					
					console.log("")
       
				},
				timeout: (xhr, editor) => {
					// 
				},
				error: (xhr, editor) => {
					// 
				},
				customInsert: (insertImg, result, editor) => {
                    // 
                    
					//resultdata[{url:""},...]
					//insertImg()
                    var url=result.data[0];  //
                    url=url.substring(6);   //public
					insertImg(this.url+url);
				}
			}
			
        // 
			this.editor.customConfig.onchange = (html) => {
                
                // html
                this.content=html.trim();
                
                // this.editor.txt.html();
                this.editor.txt.html(this.content);

                // 
                this.sendToParent(this.content);
			}
			this.editor.customConfig.zIndex = 2000
            // ;
            this.editor.create();
        },

        // 
             //  this.$emit(, )
        sendToParent(content){
            this.$emit("editorData", content)
        },

        // 
            // flag--- props
            // :
            //     1.ref,<child ref="myData" ></child>
            //     2.,:  this.$refs.myData.chidMethods()
            //     3. chidMethods(val){ val }
        editorClear(flag){
            if(flag==true){
                this.editor.txt.clear()
            }
        },

        // 
        // 1.--
        // 2.--
        //  @content
        //  @flag;false
        checkData(content,flag){
           
            if(content){
                //flag
                this.content=content;   
                this.editor.txt.html(this.content);   //
                this.editor.$textElem[0].contentEditable=flag;   //
            }
        }
    }
}
</script>
<style scoped>
    .editor{
		border: 1px solid #DCDFE6;
    }
    /deep/ .w-e-text{
        min-height: 100px !important;
    }
</style>


