
<!-- 
    ,idid
-->
<template>
    <div style="width: 50%;">
        <header>
            <router-link to="/all"></router-link> >>> 
        </header>
        <el-form label-width="80px" :model="form" ref="form" class="demo-ruleForm my_inp">
            <el-form-item label="" >
                <el-input v-model="form.title" readonly="readonly"></el-input>
            </el-form-item>
              <el-form-item label="" >
                <el-input v-model="form.kind" readonly="readonly"></el-input>
            </el-form-item>

           <el-form-item label="">
               <!--  -->
                <editor ref="checkDatas"></editor>

            </el-form-item>

            <el-form-item label="" class="my_inp">
                <el-input type="text" v-model="form.writer" readonly="readonly"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="back()"></el-button> 
            </el-form-item>
        </el-form>
    </div>
   
</template>
<script>
import editor from '../../repeatModule/editor'

export default {
    name:"check",
    data(){
        return{
            queryId:'',  //id
            form:{},   //
            url:"http://127.0.0.1:3000",
           
        }
    },
    components: {
        editor
    },
    created(){
        this.getRoutId();
    },
    methods: {
        // id
        getRoutId(){
           this.queryId=this.$route.query.id;   //id
            //  id
           this.$axios.get(this.url+"/details",{params:{
               id:this.queryId
           }})
           .then(result=>{
               if(result.data){
                    this.form=result.data;
                    // 
                    this.$refs.checkDatas.checkData(this.form.content,false);
                    // console.log(result)
               }
                
               
           })
           .catch(err=>{
                this.$message({
                    message:err.message,
                    type:"error",
                    durations:1000,
                })
           })
        },
        // 
        back(){
            this.$router.go(-1)
        },
    }
}
</script>
<style scoped>
    header{
        padding: 0 0 20px;
        font-size: 14px
    }
    header a{
        color: #409EFF
    }

    .el-textarea__inner{
	    resize: none;
    }
    .ql-container{
		height: 500px;
	}
    /deep/ .ql-container{
		height: 500px;
	}
	/deep/ .ql-editor img{
		width: 20%;
		height:20%;
	}
	/deep/ .my_inp input{
		width: 50%
	}
</style>


